import classes from "./ProjectGrid.module.css";
import ImageButton from "../components/ImageButton";
import playStore from "../assets/other/playstore.png";
import web from "../assets/other/www.webp";
import github from "../assets/other/github.webp";

const ProjectGrid = (props) => {
    const redirredirectHandlerect = (url) => {
        fetch(
            "https://discord-webhooks-analytics.onrender.com/portfolio/action",
            {
                method: "POST",
                body: JSON.stringify({
                    event: `Project ${props.data.name}`,
                    link: url,
                    userId: props.userId,
                }),
                headers: {
                    "Content-Type": "application/json",
                },
            }
        ).catch((err) => {});
        window.open(url);
    };

    return (
        <div className={classes.container}>
            <img src={props.data.image} alt={props.data.name} />
            <h3>{props.data.name}</h3>
            <p>{props.data.detail}</p>
            <div className={classes.links}>
                {props.data.playStore && (
                    <ImageButton
                        src={playStore}
                        onClick={redirredirectHandlerect.bind(
                            null,
                            props.data.playStore
                        )}
                    />
                )}
                {props.data.webLink && (
                    <ImageButton
                        src={web}
                        onClick={redirredirectHandlerect.bind(
                            null,
                            props.data.webLink
                        )}
                    />
                )}
                {props.data.github && (
                    <ImageButton
                        src={github}
                        onClick={redirredirectHandlerect.bind(
                            null,
                            props.data.github
                        )}
                    />
                )}
            </div>
        </div>
    );
};

export default ProjectGrid;
